* {
  box-sizing: border-box;
}

html {
  overflow-y: -moz-scrollbars-vertical;
  overflow-y: scroll;
}

body {
  min-width: 360px;
  position: relative;
  font-family: $main-font;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: 2;
  overflow-x: hidden;
  color: $white;
  background-color: $dark-grey;
}

main {
  align-items: flex-start;
  flex: 1 0 auto;
  justify-content: flex-start;
  z-index: 2;
  margin: 0 auto;
  width: 100%;
  overflow: hidden;
}
.main-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  overflow: hidden;
  position: relative;
}

svg path {
  transition: fill 0.4s ease-out, stroke 0.4s ease-out;
}

.d-flex {
  display: flex;

  &-column-center {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.h-100 {
  height: 100%;
}

pre {
  color: white;

  .white_bg & {
    color: $black;
  }
}

button, input, optgroup, select, textarea {
  font-size: inherit;
}

picture {
  display: flex;
}

img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.img {
  &-mobile {
    display: none;
  }
}

.custom-logo-mobile {
  display: none;

  img {
    transition: width 0.3s ease-out;
  }
}

section {
  width: 100%;
  position: relative;
  z-index: 2;
  overflow: hidden;
}

.container {
  margin: 0 8.33vw;
  padding-left: 30px;
  padding-right: 30px;
  width: 100%;
  display: flex;
  position: relative;
  z-index: 2;
}

.position-relative {
  position: relative;
}
.position-absolute{
  position: absolute;
}

button {
  font-size: 14px;
  padding: 0;
  border: none;
  outline: none;
  background-color: transparent;

  &:focus, &:active, &:focus-visible {
    outline: none;
  }
}

.d-flex {
  display: flex;

  &-column {
    display: flex;
    flex-direction: column;
  }

  &-column-center {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &-align-bottom {
    display: flex;
    align-items: flex-end;
  }
}

svg {
  path {
    transition: fill 0.3s ease;
  }
}

.bg-image {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  picture {
    width: 100%;
    height: 100%;
  }
}

 .prevent-change{
   transition: all 1s cubic-bezier( .42, 0, .58, 1 );
 }

@media screen and (max-width: $breakpoint-xl) {
  .container {
    max-width: 1160px;
  }
}

@media screen and (max-width: $breakpoint-l) {
  .custom-logo-mobile {
    display: flex;
  }
}

@media screen and (max-width: $breakpoint-md) {
  body {
    font-size: 16px;
  }
}

@media screen and (max-width: $breakpoint-sm) {

  .container {
    padding-left: 15px;
    padding-right: 15px;
  }

}