.scene {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: $first-screen-bg;

  .layer {
    width: 100%;
    height: 100%;
    z-index: 3;

    &.z-index-0 {
      z-index: 0;
    }

    &.z-index-1 {
      z-index: 1;
    }

    &.z-index-2 {
      z-index: 2;
    }

    &.z-index-3 {
      z-index: 3;
    }

    &.z-index-4 {
      z-index: 4;
    }
  }

  &::after {
    content: '';
    background-image: url("../picture/ton.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 1;
  }

  &__element {
    position: absolute;
    width: 100%;
    height: 100%;

    &--moon {
      z-index: 3;
    }

    &--ship {
      z-index: 4;
    }
  }

  &__moon {
    position: absolute;
    right: 9%;
    top: 3%;
    animation: moonTranslate 160s linear forwards;

  }

  &__clouds {
    position: absolute;
    top: 3%;
    left: 1%;
    width: 100%;

    svg {
      width: 100%;
      height: auto;

      path {
        &:nth-child(1) {
          animation: cloudsMover 16s infinite linear;
        }

        &:nth-child(2) {
          animation: cloudsMover 12s infinite linear;
        }

        &:nth-child(3) {
          animation: cloudsMover 10s infinite linear;
        }

        &:nth-child(4) {
          animation: cloudsMover 12s infinite linear;
        }
      }
    }
  }

  &__north-shine {
    position: absolute;
    top: 0;
    left: -1%;
    width: 101%;
    max-width: 1720px;
    filter: url(#water-filter);
    animation: animateShine 12s linear infinite;

    &__img {
      width: 100%;
      min-height: 680px;
    }
  }

  &__stars {
    position: absolute;
    left: -7.4%;
    top: 0;
    width: calc(100% + 7.4%);

    &__animation {
      position: absolute;
      top: 0;
      z-index: 1;

      svg {
        width: 100%;
        height: auto;

        g {
          path {
            animation: starsFade 6s ease infinite;
          }


          &:nth-child(1n) {
            path {
              animation-delay: 1s;
            }
          }

          &:nth-child(2n) {
            path {
              animation-delay: 3s;
            }
          }

          &:nth-child(3n) {
            path {
              animation-delay: 5s;
            }
          }
        }
      }
    }

    &__falling {
      span {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 4px;
        height: 4px;
        background: #fff;
        border-radius: 50%;
        box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.1), 0 0 0 8px rgba(255, 255, 255, 0.1), 0 0 20px rgba(255, 255, 255, 0.1);
        animation: animate 3s linear infinite;

        &::before {
          content: '';
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          width: 300px;
          height: 1px;
          background: linear-gradient(90deg, #fff, transparent);
        }
        &:nth-child(1){
          top: -15%;
          left: 26%;
          animation-delay: 0s;
          animation-duration: 11s;
        }
        &:nth-child(2){
          top: -5%;
          right: 60%;
          left: initial;
          animation-delay: 4s;
          animation-duration: 7s;
        }
        &:nth-child(3){
          top: -5%;
          right: 45%;
          left: initial;
          animation-delay: 0.8s;
          animation-duration: 8s;
        }
        &:nth-child(4){
          top: -10%;
          right: 48%;
          left: initial;
          animation-delay: 2s;
          animation-duration: 9s;
        }
        &:nth-child(5){
          top: -8%;
          right: 18%;
          left: initial;
          animation-delay: 2s;
          animation-duration: 10s;
        }
        &:nth-child(6){
          top: -12%;
          right: 5%;
          left: initial;
          animation-delay: 1s;
          animation-duration: 9s;
        }
        &:nth-child(7){
          top: -5%;
          right: -12%;
          left: initial;
          animation-delay: 3s;
          animation-duration: 8s;
        }
        &:nth-child(8){
          top: -5%;
          right: 32%;
          left: initial;
          animation-delay: 4s;
          animation-duration: 10s;
        }
      }

      @keyframes animate {
        0% {
          transform: rotate(315deg) translateX(0);
          opacity: 1;
        }
        70% {
          opacity: 1;
        }
        100% {
          transform: rotate(315deg) translateX(-1000px);
          opacity: 0;
        }
      }
    }

  }

  &__ice {
    position: absolute;
    left: 0;
    bottom: 10%;
    width: 100%;
    height: 100%;
    z-index: 2;

    &__element {
      position: absolute;
      width: fit-content;
      height: fit-content;
      z-index: 1;
    }

    &__wrapper {
      bottom: 0;
      right: 0;
      position: absolute !important;
      z-index: 2;

    }

    @for $i from 1 through 23 {
      &-#{$i} {
        animation: iceMover (random(10) + 3s) infinite linear;
      }
    }

    &-0 {
      left: 1%;
      bottom: 30%;
    }

    &-1 {
      left: -1%;
      bottom: 53%;
    }

    &-2 {
      right: 33.5%;
      bottom: 59%;
      z-index: 0;
    }

    &-3 {
      right: 32.5%;
      bottom: 8%;
    }

    &-4 {
      right: 0.5%;
      bottom: 24%;
      z-index: 1;
    }

    &-5 {
      right: 0.5%;
      bottom: 7%;
    }

    &-6 {
      right: 3.5%;
      bottom: -23%;
    }

    &-7 {
      right: 14%;
      bottom: 12%;
    }

    &-8 {
      right: 17.5%;
      bottom: 26%;
      z-index: 1;
    }

    &-9 {
      right: 26.5%;
      bottom: -6%;
    }

    &-10 {
      right: 37.5%;
      bottom: -26%;
    }

    &-11 {
      bottom: 58%;
      left: 45%;
    }

    &-12 {
      bottom: 73%;
      left: 40.9%;
    }

    &-13 {
      bottom: 25%;
      left: 31%;
    }

    &-14 {
      bottom: 41%;
      left: 25%;
    }

    &-15 {
      bottom: 3%;
      left: 34.1%;
    }

    &-16 {
      bottom: 1%;
      left: 27.1%
    }

    &-17 {
      bottom: -16%;
      left: 16.1%;
    }

    &-18 {
      bottom: 17%;
      left: 15%;
    }

    &-19 {
      bottom: -8%;
      left: -1.5%;
    }

    &-20 {
      bottom: 64%;
      left: 9.1%;
    }

    &-21 {
      bottom: -23%;
      left: 39.5%;
    }

    &-22 {
      bottom: 41%;
      left: 52%;
    }

    &-23 {
      left: 6.3%;
      bottom: 35%;
    }
  }

  &__rocks {
    position: absolute;
    bottom: 13vw;
    left: -7%;
    width: 100%;
    max-width: 1680px;
    z-index: 1;

    svg {
      width: 100%;
      height: auto;
    }
  }

  &__ship {
    position: absolute;
    bottom: 23px;
    right: -9%;
    z-index: 1;
    animation: mover 5s 0.1s infinite linear;
    width: fit-content;
    height: fit-content;

    &__decor {
      position: absolute;
      width: 715px;
      height: fit-content;
      bottom: 0;
      right: 0;
      z-index: 2;
    }
  }

  &__water {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;

    &__main {
      width: 120%;
      margin-left: -9%;
      display: flex;
      height: auto;
      position: absolute;
      top: 0;
      z-index: 0;

      svg {
        width: 100%;
        height: auto;
      }
    }

    &__img {
      position: relative;
      z-index: 1;
      width: 118%;
      height: auto;
      margin-left: -9%;
    }
  }

  @media screen and (min-height: 1080px) and (min-width: 2100px) {
    &__north-shine {
      max-width: 100vw;
    }
  }
  @media screen and (min-width: 2800px) and (min-height: 1400px) {
    &__ice__element svg {
      transform: scale3d(2.2, 2.2, 2.2);
    }
    &__ship {
      bottom: 34%;
      right: 4%;

      svg {
        transform: scale3d(1.8, 1.8, 1.8);
      }

      &__decor {
        transform: scale3d(1.8, 1.8, 1.8);
        right: 3%;
      }
    }
    &__rocks {
      bottom: 13vw;
      max-width: 83vw;
    }
    &__moon {
      top: 5%;

      svg {
        transform: scale3d(1.1, 1.1, 1.1);
      }
    }
  }
  @media screen and (max-width: 2100px) and (min-height: 1080px) {
    &__rocks {
      bottom: 13vw;
      max-width: 1920px;
    }
  }
  @media screen and (min-width: 2100px) and (max-width: 2800px) {
    &__ice__element svg {
      transform: scale3d(1.6, 1.6, 1.6);
    }
    &__ship {
      bottom: 42%;
      right: 0;

      svg {
        transform: scale3d(1.6, 1.6, 1.6);
      }

      &__decor {
        transform: scale3d(1.3, 1.3, 1.3);
        right: 3%;
      }
    }
    &__rocks {
      bottom: 14vw;
      max-width: 1920px;
    }
  }
  @media screen and (min-width: 1921px) and (max-width: 2100px) {
    &__ice__element svg {
      transform: scale3d(1.3, 1.3, 1.3);
    }
    &__ship {
      bottom: 25%;
      right: -2%;

      svg {
        transform: scale3d(1.1, 1.1, 1.1);
      }
    }
    &__rocks {
      bottom: 27%;
    }
  }
  @media screen and (min-width: 1600px) and (max-width: 1921px) {
    &__ice__element svg {
      transform: scale3d(1.1, 1.1, 1.1);
    }
    &__ship {
      bottom: 23%;
      right: -6%;

    }
    &__moon {
      top: -1%;

      svg {
        transform: scale3d(0.9, 0.9, 0.9);
      }
    }
    &__rocks {
      bottom: 13.5vw;
    }
  }
  @media screen and (min-width: 1366px) and (max-width: 1800px) and (max-height: 850px) {
    &__ice__element svg {
      transform: scale3d(0.9, 0.9, 0.9);
    }
    &__moon {
      top: 3%;

      svg {
        transform: scale3d(0.9, 0.9, 0.9);
      }
    }
    &__ship {
      bottom: 0;
      right: -12%;

      svg, &__decor {
        transform: scale3d(0.8, 0.8, 0.8);
        right: -12%;
      }
    }
    &__rocks {
      bottom: 23.5%;
      max-width: 1360px;
    }
  }
}