@keyframes mover {
  0% {
    transform: translate3d(0, 0, 0);
  }
  25% {
    transform: translate3d(-0.5%, -1%, -40px)
  }
  50% {
    transform: translate3d(0, 0, 0);
  }
  75% {
    transform: translate3d(0.5%, 1%, 40px)
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes iceMover {
  0% {
    transform: translate3d(0, 0, 0);
  }
  25% {
    transform: translate3d(-0.5%, -10%, -40px)
  }
  50% {
    transform: translate3d(0, 0, 0);
  }
  75% {
    transform: translate3d(0.5%, 10%, 40px)
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes cloudsMover {
  0% {
    transform: translate3d(0, 0, 0);
  }
  25% {
    transform: translate3d(-2%, -1%, -100px)
  }
  50% {
    transform: translate3d(0, 0, 0);
  }
  75% {
    transform: translate3d(2%, 1%, 40px)
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}


@keyframes starsFade {
  0% {
    fill-opacity: 1
  }
  50%{
    fill-opacity: 0.1
  }
  100% {
    fill-opacity: 1
  }
}

@keyframes moonTranslate {
  0% {
    transform: translate3d(0,0,0);
  }
  50%{
    transform: translate3d(-30vw,-8vw,0);
  }
  100%{
      transform: translate3d(-70vw,-5vw,-100px);
  }
}


@keyframes animateShine {
  from {background-position: 0% 0%}
  to {background-position: 100% 100%}
}