.btn {
  transition: all 0.4s ease;
  cursor: pointer;
  white-space: nowrap;
  position: relative;
  z-index: 1;
  align-items: center;
  width: fit-content;
  padding: 20px 50px;
  display: flex;
  justify-content: center;
  font-style: normal;
  font-size: clamp(1.5rem, 0.833vw + 0.5rem, 2.5rem);
  line-height: clamp(1.725rem, 0.958vw + 0.575rem, 2.875rem);
  text-align: center;
  border: none;
  color: #fff;
  background: $red;
  margin-right: 40px;
  span svg{
    margin-left: 15px;
    width: 22px;
    height: 18px;
  }

  @media screen and (max-width: $breakpoint-sm) {
    font-size: 16px;
  }
  @media (hover:hover) {
    &:hover {
      background-position: right top;
    }
  }
  @media  screen and (max-width: 1920px) {
    font-size: 24px;
  }

}