.intro {
  display: flex;
  align-items: center;
  .container{
    width: 55.6%;
    z-index: 4;
  }

  &__title {
    text-align: start;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 0;
    &__button {
      width: 68px;
      height: 68px;
      background-color: $red;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 200px;
      padding: 5px;
      margin-left: 28px;
      position: relative;

      &::after {
        content: '';
        position: absolute;
        left: 5px;
        right: 5px;
        width: calc(100% - 12px);
        height: calc(100% - 12px);
        border-radius: 89px;
        border: 1px solid #ffffff;
      }
    }
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 4vw;
    &__awards{
      display: flex;
      align-items: center;
      gap: 20px;
      &__img{
        height: 100%;
        width: 8.5vw;
      }
    }
  }
  &__subtitle{
    margin-top: 0;
    margin-bottom: 0;
    text-transform: uppercase;
  }
}