.header {
  position: fixed;
  top: 0;
  z-index: 3;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: $white;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);

  &__container {
    display: flex;
    width: 100%;
    justify-content: space-between;
    margin: 0 8.33vw;
    padding-top: 50px;
    padding-bottom: 46px;
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    border-right: 1px solid rgba(255, 255, 255, 0.1);
  }

  &__calc {
    display: flex;
    align-items: center;
    padding-left: 45px;

    &__icon {
      display: flex;
    }

    &__text {
      margin-left: 16px;
      font-size: 13px;
      line-height: 1.4;
      text-align: start;
    }
  }

  &-cta-button {
    font-size: 16px;
    line-height: 1.5px;
    display: flex;
    align-items: center;
    padding: 10px 13px;
    cursor: pointer;
    position: relative;

    span {
      margin-left: 9px;
    }

    &::after {
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 63%;
      background-color: $red;
      z-index: -1;
    }
  }

  .custom-logo-link {
    display: flex;
    margin-top: -12px;
    padding-left: 7px;
  }

  @media screen and (min-width: 2100px) {
    &__calc__text {
      font-size: 15px;
    }
    &-cta-button {
      font-size: 18px;
    }
  }
}

.menu {

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -50px;
  margin-bottom: -46px;
  padding-top: 50px;
  padding-bottom: 46px;
  padding-left: 30px;
  padding-right: 45px;
  width: 60.4%;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
  border-right: 1px solid rgba(255, 255, 255, 0.1);

  &-inner {
    display: flex;
    align-items: center;
    gap: 55px;
    justify-content: space-between;
  }

  &-link {
    font-size: 18px;
    line-height: 1.5;

    &-icon {
      opacity: 0;
      transition: opacity 0.3s ease-out;
      margin-left: 4px;
    }

    @media (hover: hover) {
      &:hover {
        .menu-link-icon {
          opacity: 1;
        }
      }
    }
  }

  @media screen and (min-width: 2100px) {
    &-link {
      font-size: 20px;
    }
  }
}
